import React, { useEffect, useReducer, useState } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import Photo from "entities/Photo";

const NiceLoadingImage: React.FC<{ image: Photo }> = ({ image }) => {
  // const forceUpdate = React.useReducer(() => ({}), {})[1] as () => void;
  // useEffect(forceUpdate, [image]);
  const [img, setImg] = useState<Photo>();
  useEffect(() => {
    setImg(image);
  }, [image]);
  return <LazyLoadImage alt={img.key} effect='blur' src={img.imgSrc} width='100%' />;
};

export default NiceLoadingImage;
